<template>
  <div class="d-flex text-right justify-content-center">
    <b-card
      style="max-width: 20rem;"
      align="center"
      class="mb-2"
    >
      <b-card-text align="left">
        Enter your password.
      </b-card-text>
      <b-form-input 
        :type="showPassword ? 'text' : 'password'"
        v-model="passwordValue"
        placeholder="Please key in your password..."
      >
      </b-form-input>
      <p></p>
      <b-row>
        <b-col>
          <b-button 
            @click="showPassword = !showPassword" 
            variant="success"
          >
            {{ pwToggleText }}
          </b-button>
        </b-col>
        <b-col>
          <b-button 
            variant="success"
            @click="clearPW"
          >
            Clear Password
          </b-button>
        </b-col>
      </b-row>
      <p></p>
      <PasswordStrength :strength="pwStrength()" />
    </b-card>
  </div>
</template>

<script>
import PasswordStrength from "./PasswordStrength.vue";
export default {
  components: {
    PasswordStrength,
  },
  data() {
    return {
      passwordValue: "",
      showPassword: false,
      criteriaCounter: 0,
      logicArray: [0,0,0,0,0],
    };
  },
  computed: {
    pwToggleText() {
      return this.showPassword == true ? "Hide Password" : "Show Password"
    },
  },
  methods: {
    clearPW() {
      this.passwordValue = "";
    },
    pwStrength() {
      if(this.passwordValue.match(/[A-Z]+/g)) {
        this.logicArray[0] = 1;
      } else {
        this.logicArray[0] = 0;
      }
      if(this.passwordValue.match(/[a-z]+/g)) {
        this.logicArray[1] = 1;
      } else {
        this.logicArray[1] = 0;
      }
      if(this.passwordValue.match(/[0-9]+/g)) {
        this.logicArray[2] = 1;
      } else {
        this.logicArray[2] = 0;
      }
      if(this.passwordValue.match(/[$#&_]+/g)) {
        this.logicArray[3] = 1;
      } else {
        this.logicArray[3] = 0;
      }
      if(this.passwordValue.length >= 8) {
        this.logicArray[4] = 1;
      } else {
        this.logicArray[4] = 0;
      }
      this.criteriaCounter = this.logicArray.reduce((partialSum, a) => partialSum + a, 0);
      return this.criteriaCounter;
    }
  }
};
</script>

