const state = {
    faqList: [
        {
          'id': "1",
          'question': "What is Apple?",
          'answer': "Apple",
          'visible': false,
        },
        {
          'id': "2",
          'question': "What is Boy?",
          'answer': "Boy",
          'visible': false,
        },
        {
          'id': "3",
          'question': "What is Cat?",
          'answer': "Cat",
          'visible': false,
        },
    ],
    totalRecords: 0,
    questionSelected: null,
  };
  
  const mutations = {
    setFAQList(state, payload) {
      state.faqList = payload;
    },
    setTotalRecords(state, payload) {
      state.totalRecords = payload;
    },
    setQuestionSelected(state, payload) {
      state.questionSelected = payload;
    },
    deleteItem(state, payload) {
      state.faqList = state.faqList.filter(x => x.id != payload);
    },
    addItem(state, payload) {
      let newId = Math.max(...state.faqList.map(x => x.id));
      state.faqList.push(
        {
          'id': newId+1,
          'question': payload[0],
          'answer': payload[1],
          'visible': true,
        }
      );
    }
  };
  
  export default {
    namespaced: true,
    state,
    mutations,
  };