<template>
  <b-card :bg-variant="pwStrengthColor()" style="max-width: 20rem;" align="center" class="mb-2">
    {{ passwordStrength }} Password
  </b-card>
</template>

<script>
  export default {
    props:  {   
      strength: {
        type: Number,
        required: true
      },
    },
    computed: {
      passwordStrength() {
        switch (this.strength) {
          case 5:
            return "Strong";
          case 4:
          case 3:
            return "Moderate";
          default:
            return "Weak";
        }
      }
    },
    methods: {
      pwStrengthColor() {
        switch (this.strength) {
          case 5:
            return "success";
          case 4:
          case 3:
            return "warning";
          default:
            return "danger";
        }
      }
    }
  }
</script>