<template>
  <div class="accordion" align="center" role="tablist">
    <b-card v-for="(item, idx) in faqList" :key="idx" class="d-flex" style="max-width: 50rem;">
      <b-card-header header-tag="header" class="d-flex justify-content-between align-items-center" :style="headerObject" role="tab">
          {{ item.question }}
        <b-button :key="idx+'btn'" v-b-toggle="'accordion-' + item.id" :aria-expanded="item.visible ? 'true' : 'false'" aria-controls="'accordion-' + item.id" :style="buttonObject"> {{item.visible ? '+' : '-'}}</b-button>
      </b-card-header>
      <b-collapse :key="idx+'collapse'" :id="'accordion-' + item.id" v-model="item.visible" accordion="qna-accordion" class="white" role="tabpanel">
        <b-card-body>
          <b-row align-v="center" align-h="center">
            <b-col cols="3">
              <b-card-text style="font-size: large;">{{ item.answer }}</b-card-text>
            </b-col>
            <b-col cols="3">
              <b-button @click="deleteItem(item.id)" variant="danger">Delete</b-button>
            </b-col>
          </b-row>
        </b-card-body>
      </b-collapse>
    </b-card>
    <b-card style="max-width: 50rem;">
      <b-row align-v="center" align-h="around">
        <b-col cols="10">
          <b-form-input v-model="qn" :state="qnState" placeholder="Enter your question"></b-form-input>
          <p></p>
          <b-form-input v-model="ans" :state="ansState" placeholder="Enter your answer"></b-form-input>
        </b-col>
        <b-col cols="1">
          <b-button @click="addItem()" variant="success">Add</b-button>
        </b-col>
      </b-row>
      <p v-if="qnaState"> Both question and answer are required to add into database! </p>
    </b-card>
  </div>
</template>

<script>
  import { mapState } from "vuex";
  export default {
    data() {
      return {
        headerObject: 
          {
            backgroundColor: '#0E141E',
            color: '#32C766',
            maxWidth: '20rem',
            fontSize: 'large',
          },
        buttonObject: 
          {
            backgroundColor: '#0E141E',
            color: '#32C766',
            fontSize: 'large',
          },
        qn: '',
        ans: '',
        qnaState: false,
      };
    },
    computed: {
      ...mapState({
        faqList: (state) => state.qnaPage.faqList,
        totalRecords: (state) => state.qnaPage.totalRecords,
      }),
      qnState() {
        return this.qn.length > 0 ? true : false
      },
      ansState() {
        return this.ans.length > 0 ? true : false
      },
      questionSelected: {
        get() {
          return this.$store.state.qnaPage.questionSelected;
        },
        set(newValue) {
          this.$store.commit("qnaPage/setQuestionSelected", newValue);
        },
      },
    },
    methods: {
      deleteItem(id) {
        this.$store.commit("qnaPage/deleteItem", id);
      },
      addItem() {
        if (this.ans != "" && this.qn != "") {
          this.$store.commit("qnaPage/addItem", {...[this.qn, this.ans]});
        } else {
          this.qnaState = true;
        }
      },
    }
  }
</script>

<style>
  .dark-blue {
    background-color: #0E141E;
    color: #32C766
  }
</style>